import {disableScroll, enableScroll} from "./disableScroll.js";
import {waitForScrollEnd} from "./waitForScrollEnd.js";
let runningScroll = false;
const startTime = new Date().getTime();
export const continueColumnsRender = async () => {
  const columns = [...document.querySelectorAll(".column")];
  if (!columns.length)
    return;
  const waiters = [];
  const runTime = new Date().getTime() - startTime;
  for (const column of columns) {
    const activePerson = column.querySelector(".person.active");
    if (activePerson) {
      const top = activePerson.offsetTop - 20;
      column.querySelector(".inner").scrollTo({top, behavior: runTime < 2e3 ? "auto" : "smooth"});
      waiters.push(waitForScrollEnd(column).then(() => {
        disableScroll(column.querySelector(".inner"), "y");
      }));
    } else {
      enableScroll(column.querySelector(".inner"), "y");
    }
  }
  await Promise.all(waiters);
  if (!runningScroll) {
    runningScroll = true;
    setTimeout(() => {
      document.body.scrollTo({left: document.body.scrollWidth, behavior: runTime < 2e3 ? "auto" : "smooth"});
      waitForScrollEnd(document.body).then(() => {
        runningScroll = false;
      });
    }, 600);
  }
};
